// 首页商品
import { request } from '@/api/reques.js';
import * as MemberApi from '@/api/member/member.js';
import { goWindow } from '@/api/navigate.js'; // 页面跳转
import config from '@/api/config.js';
export default {
	data() {
		return {
			data_list: [],
			page_index: 1,
			page_count: 0
		};
	},
	props: {
		options: {
			type: Object,
			default: () => {
				return {};
			}
		},
		status: {
			type: String,
			default: -1
		}
	},
	watch: {
		status(e){
			let _this = this
			_this.page_idnex = 1
			_this.status = e
			_this.getData(_this.page_idnex)
		}
	},
	

	methods: {
		// 页面跳转
		handlePric(order_id) {
			goWindow('/pages/shopDetail/index?order_id=', order_id);
		},
		handleShop(shop_id){
			if(shop_id == 0)return
			goWindow('/pages/storeDetails/storeDetails?id=',shop_id)
		},
		getData(page_index) {
			let _this = this;
			MemberApi.getOrderList({
				page_index: page_index,
				order_status: _this.status == -1 ? 'all' : _this.status
			}).then(res => {
				uni.showLoading({
					title: '加载中'
				});
				let data = res.data.data;
				for (let index in data) {
					for(let index2 in data[index].order_item_list)
					data[index].order_item_list[index2].picture.pic_cover = config.domain + data[index].order_item_list[index2].picture.pic_cover;
				}
				if (page_index > 1) {
					data = this.data_list.concat(data);
				}
				setTimeout(() => {
					this.data_list = data;
					//console.log(this.data_list)
					this.page_count = res.data.page_count;
					uni.hideLoading();
				}, 500);
			});
		},
		action(fav_id) {
			uni.showToast({
				title: '取消收藏成功',
				duration: 2000,
				success: () => {
					setTimeout(() => {
						_this.newMyPath(1);
					}, 2000);
				}
			});
		},
		action_close(order_id, index){
			let _this = this
			uni.showLoading({
				title: '取消订单中'
			})
			MemberApi.orderClose({
				order_id : order_id
			}).then( res => {
				uni.hideLoading();
				if(res.data > 0){
					uni.showToast({
						title: '取消订单成功',
						duration: 2000,
						success: () => {
							_this.data_list[index].order_status = 5
							_this.data_list[index].status_name = '订单关闭'
						}
					});
				}else{
					uni.showToast({
						title: '取消订单失败',
						duration: 2000,
						icon: "none",
						success: () => {
							
						}
					});
				}
			})
		},
		loadData(e) {
			let _this = this;
			if (_this.page_index < _this.page_count) {
				_this.page_index++;
			} else {
				return;
			}
			_this.getData(_this.page_index);
		},
		handleErr(e, index) {
			if (e.type == 'error') {
				this.data_list[index].order_item_list[0].picture.pic_cover = '/static/images/list.jpg';
			}
		},
		// 专业检测
		handleDetection(){
			goWindow('/other_pages/detectiont/Detection')
		},
		// 停止交易
		hamdleShow(){
			uni.showModal({
			    title: '提示',
			    content: '确认停止交易？停止交易后，将关闭订单',
				confirmText:"停止交易",
				confirmColor:"#2E70D2",
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		},
		// 已付款
		handlePayment(){
			goWindow("/other_pages/common/installment")
		}
	}
};
